.theme-toggle {
  width: 50px;
  height: 50px;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  padding: 0;
  transition: color .2s linear, background-color .2s linear;
  display: inline-flex;
}

